.header {
	position: relative;
	display: flex;
	margin-bottom: 50px;

	h1 {
		cursor: pointer;
		margin: 0;
		font-size: 30px;
	}

	.menu {
		position: absolute;
		top: 10px;
		right: 0;

		.link {
			border: 1px solid #333;
			padding: 10px;
			margin-left: 10px;
			background-color: #efefef;
			font-weight: bold;
			border-radius: 4px;
		}
	}
}